body {
	font: 14px "Century Gothic", Futura, sans-serif;
	margin: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	overflow: hidden;
}

ol,
ul {
	padding-left: 30px;
}

.board-row:after {
	clear: both;
	content: "";
	display: table;
}

.status {
	margin-bottom: 10px;
}

.square {
	background: #fff;
	border: 1px solid #999;
	float: left;
	font-size: 35px;
	font-weight: bold;
	line-height: 34px;
	height: 60px;
	width: 60px;
	margin-right: -1px;
	margin-top: -1px;
	padding: 0;
	text-align: center;
}

.square:focus {
	outline: none;
}

.kbd-navigation .square:focus {
	background: #ddd;
}

.game {
	display: flex;
	flex-direction: row;
}

.game-info {
	margin-left: 20px;
}

@media (max-width: 425px) {
	.game {
		align-items: center;
		flex-direction: column;
	}
	.game-info {
		padding-top: 30px;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	ol,
	ul {
		padding-left: 0px;
	}
	li {
		padding-top: 7px;
	}
}
